.title {
  padding: 20px;
  font-size: 32px;
  text-align: center;
  color: royalblue;
}
.title-contacts {
  padding: 20px;
  font-size: 32px;
  text-align: center;
  margin: 0;
}

.op-appear {
  transform: translateX(-100%);
}

.op-appear-active {
  transform: translateX(0);
  transition: transform 500ms linear;
}

.title-contacts-enter {
  opacity: 0;
  transform: translateX(-100%);
}

.title-contacts-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 200ms ease-in-out, transform 200ms ease-in-out;
}

.title-contacts-exit {
  opacity: 1;
  transform: translateX(0);
}

.title-contacts-exit-active {
  opacity: 0;
  transform: translateX(-100%);
  transition: opacity 200ms ease-in-out, transform 200ms ease-in-out;
}

.filter-enter {
  transform: scale(0);
}

.filter-enter-active {
  transform: scale(1);
  transition: transform 250ms ease-in-out;
}

.filter-exit {
  transform: scale(1);
}

.filter-exit-active {
  transform: scale(0);
  transition: transform 250ms ease-in-out;
}


.display-column {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 30%;
  border: 1px solid black;
  margin-top: 20px;
  margin-left: 20px;

}


.label {
  display: flex;
  
  justify-content: space-between;
  padding: 10px;
}


.title-logandreg {
  margin-top: 20px;
  margin-left: 20px;
}